/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text {
	clip: rect(1px, 1px, 1px, 1px);
	position: absolute !important;
	height: 1px;
	width: 1px;
	overflow: hidden;
	word-wrap: normal !important;
	/* Many screen reader and browser combinations announce broken words as they would appear visually. */
}

.screen-reader-text:focus {
	background-color: #f1f1f1;
	border-radius: 3px;
	box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
	clip: auto !important;
	color: #21759b;
	display: block;
	font-size: 17.5px;
	font-size: 1.09375rem;
	font-weight: bold;
	height: auto;
	left: 5px;
	line-height: normal;
	padding: 15px 23px 14px;
	text-decoration: none;
	top: 5px;
	width: auto;
	z-index: 100000;
	/* Above WP toolbar. */
}

/* Do not show the outline on the skip link target. */
#content[tabindex="-1"]:focus {
	outline: 0;
}

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/
.alignleft {
	display: inline;
	float: left;
	margin-right: 1.5em;
}

.alignright {
	display: inline;
	float: right;
	margin-left: 1.5em;
}

.aligncenter {
	clear: both;
	display: block;
	margin-left: auto;
	margin-right: auto;
}

/*--------------------------------------------------------------
# Widgets
--------------------------------------------------------------*/
.widget {
	margin: 0 0 1.5em;
	/* Make sure select elements fit in widgets. */
}

.widget select {
	max-width: 100%;
}

/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Grid CSS
--------------------------------------------------------------*/
@supports (grid-area: auto) {
	.site {
		display: grid;
		width: 100%;
		grid-template-rows: minmax(1fr, auto);
		grid-template-columns: minmax(1fr, auto);
		grid-template-areas: "header" "main" "sidebar" "footer";
	}
	@media screen and (min-width: 900px) {
		.site {
			display: grid;
			grid-template-rows: auto auto;
			grid-template-columns: auto auto;
			grid-template-areas: "header header" "main sidebar" "main sidebar" "footer footer";
		}
		.site-header {
			grid-area: header;
		}
		.site-main {
			grid-area: main;
		}
		.widget-area {
			grid-area: sidebar;
			padding: 1em;
		}
		.site-footer {
			grid-area: footer;
		}
		/*  .main-navigation {
        grid-area: nav;
    }*/
	}
	@media screen and (min-width: 1200px) {
		.site {
			display: grid;
			grid-auto-rows: auto;
			grid-template-columns: minmax(1fr, auto);
			grid-template-areas: "header header header" "main main sidebar" "footer footer footer";
		}
	}
}

/*--------------------------------------------------------------
## Global layout
--------------------------------------------------------------*/
.page {
	flex: 1;
}

.site-main {
	padding: 1em;
	max-width: 40em;
	margin: 0 auto;
	display: flex;
	flex-direction: column;
	min-height: 100vh;
}

@media screen and (min-width: 600px) {
	.site-main {
		padding: 1em 2em;
		max-width: 80em;
	}
}

/*--------------------------------------------------------------
## Archives
--------------------------------------------------------------*/
.page-header {
	padding: 1em;
	flex-wrap: wrap;
	align-items: flex-start;
}

.page-title {
	width: 100%;
	margin: 0;
	font-family: "Roboto Mono", sans-serif;
	font-size: 1.6em;
	line-height: 1.3em;
	color: #22262a;
	word-break: break-word;
}

.archive-description {
	font-family: "Roboto Mono", sans-serif;
	font-size: 90%;
}

.archive-view .post,
.archive-view .page {
	padding: 1em;
}

.archive-view .post:after,
.archive-view .page:after {
	display: block;
	margin: 4em 0;
	width: 33.33%;
	content: "";
	border-bottom: 3px solid #22262a;
}

@supports (grid-area: auto) {
	.archive-view .site-main {
		max-width: 40em;
		padding: 1em;
	}
	@media screen and (min-width: 900px) {
		.archive-view .site-main {
			max-width: none;
			padding: 8px;
			display: grid;
			grid-template-columns: repeat(2, 1fr);
			grid-gap: 8px;
			background: #cccccc;
		}
	}
	@media screen and (min-width: 900px) and (min-width: 1200px) {
		.archive-view .site-main {
			grid-template-columns: repeat(3, 1fr);
		}
	}
	@media screen and (min-width: 900px) and (min-width: 1800px) {
		.archive-view .site-main {
			grid-template-columns: repeat(3, 1fr);
		}
	}
	@media screen and (min-width: 900px) and (min-width: 2300px) {
		.archive-view .site-main {
			grid-template-columns: repeat(3, 1fr);
		}
	}
	@media screen and (min-width: 900px) {
		.archive-view .post,
		.archive-view .page {
			background: white;
		}
		.archive-view .post:after,
		.archive-view .page:after {
			display: none;
		}
		.archive-view .category-featured {
			grid-column: span 2;
		}
		.archive-view .entry-title {
			word-break: break-word;
			font-size: 2em;
		}
		.archive-view .featured-image {
			margin: -1em -1em 0;
			width: calc(100% + 2em);
		}
	}
}

/*--------------------------------------------------------------
## Header
--------------------------------------------------------------*/
.site-header {
	display: flex;
	width: 100%;
	background-color: #3e454c;
	padding: 1em;
	justify-content: center;
	flex-flow: row nowrap;
	border: 5px solid #000;
}

@media screen and (min-width: 600px) {
	.site-header {
		padding: 1em 2em;
	}
}

.site-header .header-searchform {
	display: inline-flex;
	background-color: yellow;
	margin-right: 10px;
}

.site-header .header-searchform .fa-search {
	color: yellowgreen;
}

.site-header .header-searchform .header-searchfield {
	width: 0;
}

.site-header .header-searchform .header-searchfield:focus {
	width: 100%;
}

.site-header .btn-trigger {
	display: inline-flex;
	background-color: red;
	padding: 0;
	margin: 0;
}

.custom-logo-link {
	margin-right: 1em;
	flex-shrink: 0;
}

.custom-logo-link img {
	display: block;
	height: 70px;
	width: auto;
}

.site-branding {
	display: inline-flex;
	background-color: white;
	flex: 1 0 auto;
	min-height: 70px;
}

.site-branding__text {
	display: inline-flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	margin-right: 10px;
	line-height: 1em;
	word-break: keep-all;
}

@media screen and (min-width: 600px) {
	.site-branding__text {
		flex-direction: row;
		align-items: flex-end;
	}
}

.site-branding__text .site-title {
	/*h1*/
	margin: 0;
	line-height: 1em;
	padding: 0;
	font-family: "Segoe UI", monospace;
	font-weight: 900;
}

.site-branding__text .site-title a {
	color: yellowgreen;
	text-decoration: none;
}

.site-branding__text .site-title a:hover, .site-branding__text .site-title a:focus {
	text-decoration: none;
}

.site-branding__text .site-description {
	/*p*/
	color: yellowgreen;
	margin: 0;
	padding: 0;
	font-style: italic;
}

button.btn-trigger {
	display: block;
	text-transform: uppercase;
	width: 100%;
}

#masthead .main-navigation {
	display: block;
}

#masthead .main-navigation.is-active ul {
	display: block;
}

#masthead .main-navigation ul {
	display: none;
	list-style: none;
	padding: 0;
}

#masthead .main-navigation li {
	color: cyan;
}

#masthead .main-navigation li a {
	color: pink;
	text-decoration: none;
}

#masthead .main-navigation li a:hover, #masthead .main-navigation li a:active, #masthead .main-navigation li a:focus {
	text-decoration: underline;
}

#masthead .main-navigation li a.active {
	color: white;
	background-color: black;
}

#masthead .main-navigation .is-active,
.children.is-active,
.sub-menu.is-active {
	display: block;
}

/*!
 * Hamburgers
 * @description Tasty CSS-animated hamburgers
 * @author Jonathan Suh @jonsuh
 * @site https://jonsuh.com/hamburgers
 * @link https://github.com/jonsuh/hamburgers
 */
.hamburger {
	padding: 15px 15px;
	display: inline-block;
	cursor: pointer;
	transition-property: opacity, filter;
	transition-duration: 0.15s;
	transition-timing-function: linear;
	font: inherit;
	color: inherit;
	text-transform: none;
	background-color: transparent;
	border: 0;
	margin: 0;
	overflow: visible;
}

.hamburger:hover {
	opacity: 0.7;
}

.hamburger-box {
	width: 40px;
	height: 24px;
	display: inline-block;
	position: relative;
}

.hamburger-inner {
	display: block;
	top: 50%;
	margin-top: -2px;
}

.hamburger-inner, .hamburger-inner::before, .hamburger-inner::after {
	width: 40px;
	height: 4px;
	background-color: #000;
	border-radius: 4px;
	position: absolute;
	transition-property: transform;
	transition-duration: 0.15s;
	transition-timing-function: ease;
}

.hamburger-inner::before, .hamburger-inner::after {
	content: "";
	display: block;
}

.hamburger-inner::before {
	top: -10px;
}

.hamburger-inner::after {
	bottom: -10px;
}

/*
   * 3DX
   */
.hamburger--3dx .hamburger-box {
	perspective: 80px;
}

.hamburger--3dx .hamburger-inner {
	transition: transform 0.15s cubic-bezier(0.645, 0.045, 0.355, 1), background-color 0s 0.1s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.hamburger--3dx .hamburger-inner::before, .hamburger--3dx .hamburger-inner::after {
	transition: transform 0s 0.1s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.hamburger--3dx.is-active .hamburger-inner {
	background-color: transparent;
	transform: rotateY(180deg);
}

.hamburger--3dx.is-active .hamburger-inner::before {
	transform: translate3d(0, 10px, 0) rotate(45deg);
}

.hamburger--3dx.is-active .hamburger-inner::after {
	transform: translate3d(0, -10px, 0) rotate(-45deg);
}

/*
   * 3DX Reverse
   */
.hamburger--3dx-r .hamburger-box {
	perspective: 80px;
}

.hamburger--3dx-r .hamburger-inner {
	transition: transform 0.15s cubic-bezier(0.645, 0.045, 0.355, 1), background-color 0s 0.1s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.hamburger--3dx-r .hamburger-inner::before, .hamburger--3dx-r .hamburger-inner::after {
	transition: transform 0s 0.1s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.hamburger--3dx-r.is-active .hamburger-inner {
	background-color: transparent;
	transform: rotateY(-180deg);
}

.hamburger--3dx-r.is-active .hamburger-inner::before {
	transform: translate3d(0, 10px, 0) rotate(45deg);
}

.hamburger--3dx-r.is-active .hamburger-inner::after {
	transform: translate3d(0, -10px, 0) rotate(-45deg);
}

/*
   * 3DY
   */
.hamburger--3dy .hamburger-box {
	perspective: 80px;
}

.hamburger--3dy .hamburger-inner {
	transition: transform 0.15s cubic-bezier(0.645, 0.045, 0.355, 1), background-color 0s 0.1s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.hamburger--3dy .hamburger-inner::before, .hamburger--3dy .hamburger-inner::after {
	transition: transform 0s 0.1s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.hamburger--3dy.is-active .hamburger-inner {
	background-color: transparent;
	transform: rotateX(-180deg);
}

.hamburger--3dy.is-active .hamburger-inner::before {
	transform: translate3d(0, 10px, 0) rotate(45deg);
}

.hamburger--3dy.is-active .hamburger-inner::after {
	transform: translate3d(0, -10px, 0) rotate(-45deg);
}

/*
   * 3DY Reverse
   */
.hamburger--3dy-r .hamburger-box {
	perspective: 80px;
}

.hamburger--3dy-r .hamburger-inner {
	transition: transform 0.15s cubic-bezier(0.645, 0.045, 0.355, 1), background-color 0s 0.1s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.hamburger--3dy-r .hamburger-inner::before, .hamburger--3dy-r .hamburger-inner::after {
	transition: transform 0s 0.1s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.hamburger--3dy-r.is-active .hamburger-inner {
	background-color: transparent;
